import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default {
	data() {
		return {
			status: 'loading',
			source: 0,
			addressList: [],
			page: 1,
			totalPage: 1
		}
	},
	components: {
		uniLoadMore
	},
	onLoad(options) {
		console.log(options.source);
		this.source = options.source;

	},
	onShow() {
		this.initData();
	},
	onReachBottom() {
		this.status = "loading";
		this.totalPage > this.page ? (this.page += 1, this.loadData()) : setTimeout(() => {
			this.status = "noMore"
		}, 30)
	},
	methods: {
		initData(){
			this.page=1;
			this.totalPage=1;
			this.addressList=[];
			this.loadData();
		},
		loadData() {
			let parmas = {
				uid: getApp().globalData.uid,
				newPage: this.page
			}
			this._reqw.ipost(parmas, "addressList").then(res => {
				res.result == 0 ? (this.totalPage = res.totalPage, res.dataList != undefined && res.dataList
					.length != 0 ? res.dataList.forEach(item => {
							this.addressList.push(item)
					}) : this.status = "noMore", res.totalPage == 1 ? this.status = "noMore" : this.status = 'more') : this.$api.tip(
					res.resultNote)
			}).catch(err => {})
		},
		//选择地址
		checkAddress(item) {
			if (this.source == 1) {
				//  上一页实例
				console.log(item)
				item.address=item.province+item.city+item.area+item.details;
				this.$api.prePage().addressData = item;
				this.$api.prePage().addrId = item.id;
				uni.navigateBack();
			}
		},
		addAddress(type, item) {
			this.$api.navTo(`/pagesD/person/addressManage?type=${type}&id=${JSON.stringify(item)}`)
		},
		delAddress(id, k) {
			let _this=this;
			uni.showModal({
				confirmText:"确定",
				confirmColor:"#00736B",
				content:"确定删除吗？",
				success(res) {
						
					if(res.confirm){
						_this._reqw.ipost({
							id: id
						}, "delAddr").then(res => {
							res.result == 0 ? (_this.$api.tip("删除成功"), _this.initData()) : _this.$api.tip(res.resultNote)
						}).catch(err => {})
					}else if(res.cancel){
						_this.$api.tip('取消')
					}
				}
			})
			
		},
		initData() {
			this.page = 1;
			this.totalPage = 1;
			this.addressList = [];
			this.loadData()
		}
	}
}
